import { useEffect } from '@wordpress/element';
import { findSettingsWithTypeQuery, oceanSpacing } from './utils';

export default function GenerateSpacingCSSRule() {
  const optionData = oceanCustomizePreview.options;
  const settingInstances = findSettingsWithTypeQuery(optionData, 'preview', 'ocean-spacing', 'queryWithType');

  useEffect(() => {
    const settingsKey = {
      // top: '',
      // right: '',
      // bottom: '',
      // left: '',
      // topTablet: '',
      // rightTablet: '',
      // bottomTablet: '',
      // leftTablet: '',
      // topMobile: '',
      // rightMobile: '',
      // bottomMobile: '',
      // leftMobile: '',
    };

    settingInstances.forEach((element) => {
      for (const property in element.setting_args) {
        if (element.hasOwnProperty('css')) {
          const settingId = element.setting_args[property].id;
          const currentValue = wp.customize(settingId).get();

          // Initialize settingsKey with the current values
          settingsKey[property] = currentValue;

          // Bind to setting changes
          wp.customize(settingId, (value) => {
            value.bind((newVal) => handleSettingUpdate(element, property, newVal));
          });
        }
      }
    });

    // Function to handle setting updates
    const handleSettingUpdate = (element, property, newVal) => {
      settingsKey[property] = newVal;
      updateCSSRule(element.css, settingsKey, property, element.id);
    };
  }, [settingInstances]);

  useEffect(() => {
    return () => {
      settingInstances.forEach((element) => {
        const styleElement = document.getElementById(`ocean-preview-css-${element.id}`);
        if (styleElement) {
          document.head.removeChild(styleElement);
        }
      });
    };
  }, [settingInstances]);

  return null;
}

function updateCSSRule(cssAttrs, settings, property, settingId) {
  const styleElement = document.createElement('style');
  styleElement.id = `ocean-preview-css-${settingId}`;

  const cssRule = [];

  if (cssAttrs.selector && cssAttrs.property) {

    if ( cssAttrs.property === 'border-radius' ) {

      const desktopRule = `${cssAttrs.selector} {
        border-top-left-radius: ${oceanSpacing(settings.spacingTop, settings.spacingTop, settings.spacingTop, settings.spacingTop, 'px')};
        border-top-right-radius: ${oceanSpacing(settings.spacingRight, settings.spacingRight, settings.spacingRight, settings.spacingRight, 'px')};
        border-bottom-right-radius: ${oceanSpacing(settings.spacingBottom, settings.spacingBottom, settings.spacingBottom, settings.spacingBottom, 'px')};
        border-bottom-left-radius: ${oceanSpacing(settings.spacingLeft, settings.spacingLeft, settings.spacingLeft, settings.spacingLeft, 'px')};
      }`;
      cssRule.push(desktopRule);

      if (property === 'spacingTopTablet' || property === 'spacingRightTablet' || property === 'spacingBottomTablet' || property === 'spacingLeftTablet') {
        const tabletRule = `@media (max-width: 768px) { ${cssAttrs.selector} {
          border-top-left-radius: ${oceanSpacing(settings.spacingTopTablet, settings.spacingTopTablet, settings.spacingTopTablet, settings.spacingTopTablet, 'px')};
          border-top-right-radius: ${oceanSpacing(settings.spacingRightTablet, settings.spacingRightTablet, settings.spacingRightTablet, settings.spacingRightTablet, 'px')};
          border-bottom-right-radius: ${oceanSpacing(settings.spacingBottomTablet, settings.spacingBottomTablet, settings.spacingBottomTablet, settings.spacingBottomTablet, 'px')};
          border-bottom-left-radius: ${oceanSpacing(settings.spacingLeftTablet, settings.spacingLeftTablet, settings.spacingLeftTablet, settings.spacingLeftTablet, 'px')};
        }}`;
        cssRule.push(tabletRule);
      }

      if (property === 'spacingTopMobile' || property === 'spacingRightMobile' || property === 'spacingBottomMobile' || property === 'spacingLeftMobile') {
        const mobileRule = `@media (max-width: 480px) { ${cssAttrs.selector} {
          border-top-left-radius: ${oceanSpacing(settings.spacingTopMobile, settings.spacingTopMobile, settings.spacingTopMobile, settings.spacingTopMobile, 'px')};
          border-top-right-radius: ${oceanSpacing(settings.spacingRightMobile, settings.spacingRightMobile, settings.spacingRightMobile, settings.spacingRightMobile, 'px')};
          border-bottom-right-radius: ${oceanSpacing(settings.spacingBottomMobile, settings.spacingBottomMobile, settings.spacingBottomMobile, settings.spacingBottomMobile, 'px')};
          border-bottom-left-radius: ${oceanSpacing(settings.spacingLeftMobile, settings.spacingLeftMobile, settings.spacingLeftMobile, settings.spacingLeftMobile, 'px')};
        }}`;
        cssRule.push(mobileRule);
      }

    } else if ( cssAttrs.property === 'border-width' ) {

      const desktopRule = `${cssAttrs.selector} {
        border-top-width: ${oceanSpacing(settings.spacingTop, settings.spacingTop, settings.spacingTop, settings.spacingTop, 'px')};
        border-right-width: ${oceanSpacing(settings.spacingRight, settings.spacingRight, settings.spacingRight, settings.spacingRight, 'px')};
        border-bottom-width: ${oceanSpacing(settings.spacingBottom, settings.spacingBottom, settings.spacingBottom, settings.spacingBottom, 'px')};
        border-left-width: ${oceanSpacing(settings.spacingLeft, settings.spacingLeft, settings.spacingLeft, settings.spacingLeft, 'px')};
      }`;
      cssRule.push(desktopRule);

      if (property === 'spacingTopTablet' || property === 'spacingRightTablet' || property === 'spacingBottomTablet' || property === 'spacingLeftTablet') {
        const tabletRule = `@media (max-width: 768px) { ${cssAttrs.selector} {
          border-top-width: ${oceanSpacing(settings.spacingTopTablet, settings.spacingTopTablet, settings.spacingTopTablet, settings.spacingTopTablet, 'px')};
          border-right-width: ${oceanSpacing(settings.spacingRightTablet, settings.spacingRightTablet, settings.spacingRightTablet, settings.spacingRightTablet, 'px')};
          border-bottom-width: ${oceanSpacing(settings.spacingBottomTablet, settings.spacingBottomTablet, settings.spacingBottomTablet, settings.spacingBottomTablet, 'px')};
          border-left-width: ${oceanSpacing(settings.spacingLeftTablet, settings.spacingLeftTablet, settings.spacingLeftTablet, settings.spacingLeftTablet, 'px')};
        }}`;
        cssRule.push(tabletRule);
      }

      if (property === 'spacingTopMobile' || property === 'spacingRightMobile' || property === 'spacingBottomMobile' || property === 'spacingLeftMobile') {
        const mobileRule = `@media (max-width: 480px) { ${cssAttrs.selector} {
          border-top-width: ${oceanSpacing(settings.spacingTopMobile, settings.spacingTopMobile, settings.spacingTopMobile, settings.spacingTopMobile, 'px')};
          border-right-width: ${oceanSpacing(settings.spacingRightMobile, settings.spacingRightMobile, settings.spacingRightMobile, settings.spacingRightMobile, 'px')};
          border-bottom-width: ${oceanSpacing(settings.spacingBottomMobile, settings.spacingBottomMobile, settings.spacingBottomMobile, settings.spacingBottomMobile, 'px')};
          border-left-width: ${oceanSpacing(settings.spacingLeftMobile, settings.spacingLeftMobile, settings.spacingLeftMobile, settings.spacingLeftMobile, 'px')};
        }}`;
        cssRule.push(mobileRule);
      }

    } else {

      // For desktop
      // const liveCSSRule = `${cssAttrs.selector} { ${cssAttrs.property}: ${oceanSpacing(settings[property], settings[property], settings[property], settings[property], 'px')} }`;
      // cssRule.push(liveCSSRule);

      const desktopRule = `${cssAttrs.selector} {
        ${cssAttrs.property}-top: ${oceanSpacing(settings.spacingTop, settings.spacingTop, settings.spacingTop, settings.spacingTop, 'px')};
        ${cssAttrs.property}-right: ${oceanSpacing(settings.spacingRight, settings.spacingRight, settings.spacingRight, settings.spacingRight, 'px')};
        ${cssAttrs.property}-bottom: ${oceanSpacing(settings.spacingBottom, settings.spacingBottom, settings.spacingBottom, settings.spacingBottom, 'px')};
        ${cssAttrs.property}-left: ${oceanSpacing(settings.spacingLeft, settings.spacingLeft, settings.spacingLeft, settings.spacingLeft, 'px')};
      }`;
      cssRule.push(desktopRule);

      if (property === 'spacingTopTablet' || property === 'spacingRightTablet' || property === 'spacingBottomTablet' || property === 'spacingLeftTablet') {
        const tabletRule = `@media (max-width: 768px) { ${cssAttrs.selector} {
          ${cssAttrs.property}-top: ${oceanSpacing(settings.spacingTopTablet, settings.spacingTopTablet, settings.spacingTopTablet, settings.spacingTopTablet, 'px')};
          ${cssAttrs.property}-right: ${oceanSpacing(settings.spacingRightTablet, settings.spacingRightTablet, settings.spacingRightTablet, settings.spacingRightTablet, 'px')};
          ${cssAttrs.property}-bottom: ${oceanSpacing(settings.spacingBottomTablet, settings.spacingBottomTablet, settings.spacingBottomTablet, settings.spacingBottomTablet, 'px')};
          ${cssAttrs.property}-left: ${oceanSpacing(settings.spacingLeftTablet, settings.spacingLeftTablet, settings.spacingLeftTablet, settings.spacingLeftTablet, 'px')};
        }}`;
        cssRule.push(tabletRule);
      }

      // // For tablet
      // if (property === 'spacingTopTablet' || property === 'spacingRightTablet'  || property === 'spacingBottomTablet'  || property === 'spacingLeftTablet'  ) {
      //   const tabletRule = `@media (max-width: 768px) { ${cssAttrs.selector} { ${cssAttrs.property}: ${oceanSpacing(settings[property], settings[property], settings[property], settings[property], 'px')} }}`;
      //   cssRule.push(tabletRule);
      // }

      if (property === 'spacingTopMobile' || property === 'spacingRightMobile' || property === 'spacingBottomMobile' || property === 'spacingLeftMobile') {
        const mobileRule = `@media (max-width: 480px) { ${cssAttrs.selector} {
          ${cssAttrs.property}-top: ${oceanSpacing(settings.spacingTopMobile, settings.spacingTopMobile, settings.spacingTopMobile, settings.spacingTopMobile, 'px')};
          ${cssAttrs.property}-right: ${oceanSpacing(settings.spacingRightMobile, settings.spacingRightMobile, settings.spacingRightMobile, settings.spacingRightMobile, 'px')};
          ${cssAttrs.property}-bottom: ${oceanSpacing(settings.spacingBottomMobile, settings.spacingBottomMobile, settings.spacingBottomMobile, settings.spacingBottomMobile, 'px')};
          ${cssAttrs.property}-left: ${oceanSpacing(settings.spacingLeftMobile, settings.spacingLeftMobile, settings.spacingLeftMobile, settings.spacingLeftMobile, 'px')};
        }}`;
        cssRule.push(mobileRule);
      }

      // // For mobile
      // if ( property === 'spacingTopMobile' || property === 'spacingRightMobile' || property === 'spacingBottomMobile' || property === 'spacingLeftMobile' ) {
      //   const mobileRule = `@media (max-width: 480px) { ${cssAttrs.selector} { ${cssAttrs.property}: ${oceanSpacing(settings[property], settings[property], settings[property], settings[property], 'px')} }}`;
      //   cssRule.push(mobileRule);
      // }
    }

  }

  styleElement.textContent = cssRule.join('\n');

  const existingStyleElement = document.getElementById(styleElement.id);

  if (existingStyleElement) {
    document.head.replaceChild(styleElement, existingStyleElement);
  } else {
    document.head.appendChild(styleElement);
  }
}
