import GenerateTypographyCSSRule from './typography';
import GenerateColorCSSRule from './colors';
import GenerateRangeSliderCSSRule from './range-slider';
import GenerateSpacingCSSRule from './dimension';
import GenerateCommonCSSRule from './common';
//import GenerateCommonWithTypeCSSRule from './common-with-type';
import { createRoot, render } from '@wordpress/element';
import React from 'react'; // Make sure to import React

import './custom';
import './woocommerce';
import './edd';
import './llms';
import './ld';

window.addEventListener('DOMContentLoaded', () => {
  const styleTag = document.createElement('style');

  if (typeof createRoot === 'function') {
    const root = createRoot(styleTag);
    root.render(
      <>
        <GenerateTypographyCSSRule />
        <GenerateColorCSSRule />
        <GenerateRangeSliderCSSRule/>
        <GenerateSpacingCSSRule/>
        <GenerateCommonCSSRule/>
      </>
    );
  } else {
    render(
      <>
        <GenerateTypographyCSSRule />
        <GenerateColorCSSRule />
        <GenerateRangeSliderCSSRule/>
        <GenerateSpacingCSSRule/>
        <GenerateCommonCSSRule/>
      </>,
      styleTag
    );
  }

  // Append the styleTag to the document's head or wherever you want it
  document.head.appendChild(styleTag);
});
